import { Injectable } from '@angular/core';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { planTiers } from '@core/models';
import { Observable } from 'rxjs';

@Injectable()
export class PricingFacade {
	constructor(private adsApi: AdsApi) {}

	getAdsPlan(): Observable<planTiers> {
		return this.adsApi.getAdsPrice();
	}
}
